"use client";

import React, { useState } from "react";
import { useRouter } from "next/navigation";
import {
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
} from "firebase/auth";
import { auth } from "@/lib/client"; // ðŸ‘ˆ import from your client.ts

type AuthFormProps = {
  type: "sign-up" | "sign-in";
};

const AuthForm: React.FC<AuthFormProps> = ({ type }) => {
  const [name, setName] = useState(""); // used only for sign-up
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const router = useRouter();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    try {
      if (type === "sign-up") {
        // âœ… Create new user in Firebase
        await createUserWithEmailAndPassword(auth, email, password);

        // ðŸ‘‡ Redirect to sign-in after success
        router.push("/sign-in");
      } else {
        // âœ… Sign in user
        await signInWithEmailAndPassword(auth, email, password);

        // ðŸ‘‡ Redirect to your dashboard/analyser
        router.push("/analyser");
      }
    } catch (err: any) {
      console.error("Firebase Auth Error:", err.message);
      alert(err.message);
    }
  };

  return (
      <form onSubmit={handleSubmit} className="flex flex-col gap-4">
        {type === "sign-up" && (
            <input
                type="text"
                placeholder="Name"
                value={name}
                onChange={(e) => setName(e.target.value)}
                className="input"
            />
        )}
        <input
            type="email"
            placeholder="Email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            className="input"
        />
        <input
            type="password"
            placeholder="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="input"
        />
        <button type="submit" className="btn">
          {type === "sign-up" ? "Create Account" : "Sign In"}
        </button>
      </form>
  );
};

export default AuthForm;
